export default class Rank {
    number;
    league;
    stable;
    stars;
    streakable;

    constructor(number, league, stable, stars, streakable) {
        this.number = number;
        this.league = league;
        this.stable = stable;
        this.stars = stars;
        this.streakable = streakable;
    }

    getColor() {
        switch (this.league) {
            case 'Bronze':
                return '#845440';
            case 'Silver':
                return '#9f9f9f';
            case 'Gold':
                return '#fae95e';
            case 'Platinum':
                return '#e8deba';
            case 'Diamond':
                return '#03fcec';
            case 'Legend':
                return '#f7a40b';
        }
        return '#000000';
    }
}