<template>
    <div class="hs-rank-selector">
        <svg xmlns="http://www.w3.org/2000/svg" version="1.1" width="150px"
             height="150px" xmlns:xlink="http://www.w3.org/1999/xlink">
            <polygon :fill="currentRank.getColor()"  class="hex" points="150,75 112,140 37,140 0,75 37,10 112,10"></polygon>
            <text fill="white" font-size="50" x="50%" y="50%" dominant-baseline="middle" text-anchor="middle">{{currentRank.number}}</text>
        </svg>
        <div class="select-title">League:</div>
        <select v-model="currentRankLeague" @change="onCurrentLeagueChange()">
            <option v-for="(line, index) in currentRankLeagues" v-bind:key="index">{{line}}</option>
        </select>
        <div class="select-title">Number:</div>
        <select v-model="currentRank">
            <option v-for="(line, index) in currentRanks" v-bind:key="index" :value="line">{{line.number}}</option>
        </select>
    </div>
</template>

<script>
    import RANKS from "./Ranks.js";

    export default {
        name: "HsRankSelector",
        data: function () {
            return {
                currentRankLeague: "Bronze",
                currentRank: RANKS[0]
            }
        },
        computed: {
            currentRankLeagues: function () {
                return Array.from(new Set(RANKS.map(r => r.league)));
            },
            currentRanks: function () {
                return RANKS.filter(r => r.league === this.currentRankLeague).reverse();
            }
        },
        methods: {
            onCurrentLeagueChange: function () {
                this.currentRank = this.currentRanks[0];
            },
        }
    }

</script>

<style scoped>
    .hs-rank-selector {
        width: 150px;
    }

    select {
        width: 20ch;
        height: 4ch;
        background-color: #4a4a4a;
        color: white;
        margin-bottom: 1ch;
    }

    .select-title {
        text-align: left;
    }
</style>