<template>
    <table>
        <tr style="vertical-align: top;">
            <td class="rank-selector">
                <div>Current Rank</div>
                <hs-rank-selector></hs-rank-selector>
                <div class="select-title">Star Bonus (1-100)</div>
                <input v-model="starBonus" class="text-input" type="number" min="1" max="100">
            </td>
            <td class="calc-container" rowspan="2">
                <div class="calc-choose">
                    Calculator:
                    <select v-model="calcType">
                        <option>How many games do I have to play to reach the rank</option>
                    </select>
                </div>

                <div v-if="calcType === 'How many games do I have to play to reach the rank'">
                    <table>
                        <tr style="vertical-align: top;">
                            <td class="estimated-rank-selector">
                                <div>Estimated Rank</div>
                                <hs-rank-selector></hs-rank-selector>
                                <br>
                                <div class="select-title">Winrate (0.0-100.0%)</div>
                                <input v-model="winrate" class="text-input" type="number" min="0" max="100">
                                <div class="select-title">Max games</div>
                                <input v-model="maxGames" class="text-input" type="number" min="0" max="100">
                                <br>
                                <button class="calc-button" v-on:click="runSimulation()">Simulate</button>
                            </td>
                            <td>
                            </td>
                            <td style="padding-left: 3ch">
                                <div v-for="(s, key) in simulationResults.slice(0, 5)" v-bind:key="s" :value="s">
                                    <div>{{'Simulation ' + (key + 1)}}</div>
                                    <div>{{s.text}}</div>
                                    <br>
                                </div>
                                <div v-if="simulationResults.length > 5">
                                    <div>...</div>
                                    <br>
                                    <div>{{'Simulation ' + simulationResults.length}}</div>
                                    <div>{{simulationResults[simulationResults.length - 1].text}}</div>
                                </div>
                            </td>
                        </tr>
                    </table>
                </div>
            </td>
        </tr>
    </table>
</template>

<script>
    import HsRankSelector from "../patterns/HSRankSelector";

    export default {
        components: {HsRankSelector},
        data: function () {
            return {
                starBonus: 1,
                winrate: 50,
                maxGames: 100000,
                calcType: 'How many games do I have to play to reach the rank',
                simulationResults: []
            }
        },
        methods: {
            runSimulation: function () {
                const result = [];
                const simulate = async function (simulation) {
                    for (let i = 0; i < this.maxGames; i++) {
                        1
                    }
                    simulation.text = 'Done!';
                };

                for (let i = 0; i < 100; i++)
                {
                    const simulation = {};
                    simulation.text = 'In progress...';
                    simulate(simulation);
                    result.push(simulation);
                }

                this.simulationResults = result;
            },
        },

    }
</script>

<style scoped>
    .rank-selector {
        padding: 20px;
        text-align: center;
    }

    td.star-bonus {
        padding: 20px;
    }

    input.text-input {
        width: 19ch;
        height: 3ch;
        background-color: #4a4a4a;
        color: white;
        margin-bottom: 1ch;
    }

    .calc-container {
        padding: 20px;
        vertical-align: top;
    }

    .select-title {
        text-align: left;
    }

    select {
        height: 4ch;
        background-color: #4a4a4a;
        color: white;
        margin-bottom: 1ch;
    }

    .calc-choose {
        margin-bottom: 20px;
    }

    .estimated-rank-selector {
        text-align: center;
    }

    .calc-button {
        height: 4ch;
        width: 100%;
    }
</style>